.selectbox { text-align: center; margin-top: 3rem; }
.left { height: 2rem; }
.right { display: flex; flex-wrap: wrap;  justify-content: center; width: 65rem; margin: 0 auto; }
.right > li { flex: 11%; line-height: 3rem; cursor: pointer; } 
.middle { margin: 0 1rem; height: 2rem; }
.ulList { padding: 5rem; display: flex; justify-content: center; flex-wrap: wrap; }
.selectDate { display: flex; justify-content: center; margin-top: 3rem; }
.arrowCenter { margin: 0 4rem; font-size: 2.4rem; }
.arrowLeft, .arrowRight { font-size: 2.4rem; font-weight: 300; }
